body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.indicator {
  padding-right: 5px;
}
.folderClass::before {
  content: "Folder: //";
}

.scroller-window {
  height: calc(100vh - 150px);
  overflow-y: scroll;
  overflow-x: hidden;
  padding-right: 1px;
  padding-left: 1px;
  padding-top: 1px;
}

.row.seperator hr {
  margin: 0px 0;
}
.row.seperator {
  --bs-gutter-x: 2.5rem;
  --bs-gutter-y: 0;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x));
}

.btn.expander {
  text-decoration: none;
}
.img-container::after {
  content: "SAMPLE";
  text-align: center;
  text-justify: distribute;
  min-width: 80px;
}
.img-container {
  background-color: darkgray;
  display: inline-block;
  text-align: center;
  height: 100px;
  width: 80px;
  min-width: 80px;
}

.nocaret.dropend .dropdown-toggle::after {
  display: none;
  margin-left: 0.255;
  vertical-align: 0.255;
  content: "";
  border-top: 0.3 solid transparent;
  border-right: 0;
  border-bottom: 0.3 solid transparent;
  border-left: 0.3 solid;
}
.nocaret.dropstart .dropdown-toggle::before {
  display: none;
  margin-right: 0.255;
  vertical-align: 0.255;
  content: "";
  border-top: 0.3 solid transparent;
  border-right: 0.3 solid;
  border-bottom: 0.3 solid transparent;
}

.nocaret.dropdown-toggle::after {
  display: none;
  margin-left: 0.255;
  vertical-align: 0.255;
  content: "";
  border-top: 0.3 solid;
  border-right: 0.3 solid transparent;
  border-bottom: 0;
  border-left: 0.3 solid transparent;
}
/* Overrides for breadcrumbs for FACET */
.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0;
  margin-bottom: 0.05rem;
  list-style: none;
}
.modal-dialog.modal-90w {
  width: 90%;
  max-width: 90%;
}

.breadcrumb-item + .breadcrumb-item {
  padding-left: 0.15rem;
}
.breadcrumb-item + .breadcrumb-item::before {
  float: left;
  padding-right: 0.15rem;
  color: #6c757d;
  content: var(--bs-breadcrumb-divider, "/")
    /* rtl: var(--bs-breadcrumb-divider, "/") */;
}
.breadcrumb-item.active {
  color: #6c757d;
}
