
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.head-label {
  margin-top:6px;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 520px) { 
  .facet-lean-logo {
    height: 22px;
    margin-left: 20px;
  }
  .facet-text {
    height: 12px;
    margin-left: 20px;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) { 
  .facet-lean-logo {
    height: 24px;
    margin-left: 20px;
  }
  .facet-text {
    height: 14px;
    margin-left: 20px;
  }
}
/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .facet-lean-logo {
    height: 26px;
    margin-left: 20px;
  }
  .facet-text {
    height: 16px;
    margin-left: 20px;
  }
}

/* X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) { 
  .facet-lean-logo {
    height: 28px;
    margin-left: 20px;
  }
  .facet-text {
    height: 18px;
    margin-left: 20px;
  }
}

/* XX-Large devices (larger desktops, 1400px and up) */
@media (min-width: 1400px) { 
  .facet-lean-logo {
    height: 30px;
    margin-left: 20px;
  }
  .facet-text {
    height: 20px;
    margin-left: 20px;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
